body {
  max-width: 35em;
  text-justify: inter-word;
  text-align: justify;
  text-rendering: optimizelegibility;
  font-kerning: normal;
  margin: 0 auto;
  padding: 50px;
  font-family: Georgia, Times New Roman, Times, serif;
  font-size: 20px;
  line-height: 1.7em;
}

.reset-button {
  position: absolute;
  bottom: 20px;
  left: 10px;
}

@keyframes strikethrough {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

.strikethrough {
  position: relative;
}

.strikethrough:after {
  content: " ";
  width: 100%;
  height: 3px;
  background: #73af55;
  animation-name: strikethrough;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  position: absolute;
  top: 50%;
  left: 0;
}

.goal-message {
  width: 100%;
  text-align: center;
  white-space: nowrap;
  position: absolute;
  top: 10px;
}

.goal-met-message {
  width: 100%;
  text-align: center;
  white-space: nowrap;
  position: absolute;
  bottom: 20px;
}

@keyframes bg-fade-inout {
  0% {
    background-color: rgba(0, 0, 0, 0);
  }

  60% {
    background-color: rgba(255, 255, 255, .2);
  }

  100% {
    background-color: rgba(0, 0, 0, 0);
  }
}

.goal-container {
  width: 100%;
  height: 100%;
  pointer-events: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.goal-container-animation {
  animation: bg-fade-inout 2.5s;
}

.goal-green {
  color: #73af55;
}

.goal-red {
  color: #c02a26;
}

.img-with-credits {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.img-with-credits > .credits {
  font-size: .6em;
}

.bottom-right-ui {
  text-align: left;
  max-width: 30%;
  align-items: right;
  flex-direction: column;
  display: flex;
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.bottom-right-ui input {
  width: 40%;
}

.bottom-right-ui > div {
  height: 1.5em;
  justify-content: flex-end;
  display: flex;
}

.bottom-right-ui > * {
  margin-bottom: .5em;
}

.red-border {
  border: 1.8px inset red;
  border-radius: 1px;
}

@keyframes dash {
  0% {
    stroke-dashoffset: 1000px;
  }

  60% {
    stroke-dashoffset: 0;
  }

  100% {
    stroke-dashoffset: 1000px;
  }
}

@keyframes dash-check {
  0% {
    stroke-dashoffset: -100px;
  }

  60% {
    stroke-dashoffset: 900px;
  }

  100% {
    stroke-dashoffset: -100px;
  }
}

.goal-icon {
  width: 60px;
  height: 60px;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.goal-icon .path {
  stroke-dasharray: 1000;
  stroke-dashoffset: 0;
}

.goal-icon-animation .path.circle {
  animation: dash 2.5s ease-in-out forwards;
}

.goal-icon-animation .path.line {
  stroke-dashoffset: 1000px;
  animation: dash 2.5s ease-in-out .35s forwards;
}

.goal-icon-animation .path.check {
  stroke-dashoffset: -100px;
  animation: dash-check 2.5s ease-in-out .35s forwards;
}

.goal-met-icon .path {
  stroke: #73af55;
}

.goal-failed-icon .path {
  stroke: #c02a26;
}

.canvas-container {
  color: #fff;
  width: 100%;
  margin-bottom: 1em;
  position: relative;
}

.canvas-container canvas {
  width: 100%;
}

.tooltip {
  background-color: rgba(204, 204, 204, .667);
  border-radius: 10px;
  margin: 0;
  padding: .2em;
  font-family: Arial, Helvetica, sans-serif;
  position: fixed;
  top: 100px;
  left: 100px;
}

aside {
  background-color: #eee;
  border: 3px solid gray;
  border-radius: 10px;
  margin: .5em 0;
  padding: .2em 1em;
}

aside.info {
  border-color: #2a93cc;
}

aside.info:before {
  content: "ℹ️";
}

aside.warn {
  border-color: #ffb87d;
}

aside.warn:before {
  content: "⚠️";
}

[v-cloak] {
  display: none;
}

